<template>
 <div class="projectDetails">
   <div class="projectTitle">{{ProjectInfo.projectName}}</div>
   <div class="project-main">
     <div class="project-title">&nbsp;>&nbsp;<span style="color: #f1b954"></span></div>
        <div class="project-tab">
          <el-tabs v-model="activeName">
            <el-tab-pane label="" name="financingSituation"></el-tab-pane>
            <el-tab-pane label="" :name="ProjectInfo.assetType!=2?'ProjectInformation':'ProjectInfoDisperse'"></el-tab-pane>
            <el-tab-pane label="" :name="ProjectInfo.assetType!=2?'LeaseInfoFocus':'LeaseInfoDisperse'"></el-tab-pane>
            <el-tab-pane label="" name="madeProducts"></el-tab-pane>
            <el-tab-pane label="" name="assetInformation"></el-tab-pane>
          </el-tabs>
        </div>
     <div class="project-content">
       <transition name="component-fade" mode="out-in">
       <keep-alive>
         <component :is="activeName">
           <!--  -->
         </component>
       </keep-alive>
       </transition>
     </div>
   </div>
 </div>
</template>
<script>
  //
  import FinancingSituation from 'components/base-financingSituation/financingSituation'
  //--
  import ProjectInformation from 'components/base-ProjectInformation/ProjectInformation'
  //--
  import ProjectInfoDisperse from 'components/base-ProjectInformation/projectInfoDisperse'
  //--
  import LeaseInfoFocus from 'components/base-leaseInformation/leaseInfoFocus'
  //--
  import LeaseInfoDisperse from 'components/base-leaseInformation/leaseInfoDisperse'
  //
  import MadeProducts from 'components/base-madeProducts/madeProducts'
  //
  import AssetInformation from 'components/base-assetInformation/assetInformation'
  //ajax
  import Axios from 'axios'
  //
  import until from '../../common/util.js'
  //
  import { mapState } from 'vuex'
  //
  const ERRORCODE_CODE = 'SUCCESS'

  export default {
    data () {
      return {
      	activeName:'financingSituation',
        ProjectInfo: {}
      }
    },
    computed: {
      ...mapState({
        project_Id(state) {
          if(state.projectId === '') {
            this.$store.dispatch('getLocalStorage', 'projectId')
          }
          return state.projectId
        }
      })
    },
    created () {
      this.getProjectInformation()
    },
    methods: {
      getProjectInformation () {
        Axios.post('projectInfo/detailProject.do', {
         id: this.project_Id
        })
          .then(
            (response) => {
              let res = response.data
              if (res.errorCode === ERRORCODE_CODE) {
                this.ProjectInfo = res.data
              }
            })
          .catch(
            (error) => { console.log(error) }
          )
      }
    },
    components: {
       //
      'financingSituation': FinancingSituation,
      //--
      ProjectInformation,
      //--
      ProjectInfoDisperse,
      //--
      'LeaseInfoFocus': LeaseInfoFocus,
      //--
      'LeaseInfoDisperse': LeaseInfoDisperse,
      //
      'madeProducts': MadeProducts,
      //
      'assetInformation': AssetInformation
    }
  }
</script>
<style>
  .el-tabs__item{
    font-size: 18px !important;
  }
</style>
<style scoped>
  /**/
  .component-fade-enter-active, .component-fade-leave-active {
    transition: all .4s ease;
  }
  .component-fade-enter, .component-fade-leave-active {
    opacity: 0;
    transform: translate(10px);
  }
  .projectTitle{
    font-size:24px;
    color:#696969;
    margin-bottom: 30px;
    margin-top:-23px;
  }
  .project-main{
    background: #fff;
    padding-left:45px;
    padding-right:43px;
  }
  .project-title{
    height: 80px;
    line-height: 80px;
    background: #f7f7f7;
    font-size: 20px;
    color: #696969;
    text-align: left;
    margin-left:-45px;
    margin-right:-43px;
    padding-left:45px;
  }
  .project-tab{
    height: 60px;
    line-height: 60px;
  }
  .project-content{
    margin-top:20px;
  }
</style>



