 /*
    * 
    * number
    * decimals
    * dec_point
    * thousands_sep
    * roundtag: "round" ,"floor","round" 
    * */
until.number_format=function(number, decimals,roundtag, dec_point, thousands_sep) {
   
    number = (number + '').replace(/[^0-9+-Ee.]/g, '');
    roundtag = roundtag || "round";
    var n = !isFinite(+number) ? 0 : +number,
        prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
        sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
        dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
        s = '',
        toFixedFix = function (n, prec) {
 
            var k = Math.pow(10, prec);
            return '' + parseFloat(Math[roundtag](parseFloat((n * k).toFixed(prec*2))).toFixed(prec*2)) / k;
        };
    s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
    var re = /(-?\d+)(\d{3})/;
    while (re.test(s[0])) {
        s[0] = s[0].replace(re, "$1" + sep + "$2");
    }
 
    if ((s[1] || '').length < prec) {
        s[1] = s[1] || '';
        s[1] += new Array(prec - s[1].length + 1).join('0');
    }
    return s.join(dec);
}
//
until.thousandBit = function(s) {
	if(s == '0' || !s){
		return '0.00';
	}else if(s < 10000){
		return s;
	}else{
		return s/10000;
	}
}
//
until.thousandBitInt = function(s) {
	if(s == '0' || !s){
		return '0.00';
	}else{
		return s/10000;
	}
}

//
until.thousandBitFraction = function(s) {
	if(s == '0' || !s || s < 50){
		return '0.00';
	}else{
		//
		var n1 = Math.round(s / 100);
		//100
		var n2 = n1 / 100;
		//
		return until.thousandBitSeparatorInt(n2);
	}
}

//
//,,
until.thousandBitSeparatorInt = function(s) {
	if(s == '0'){
		return '0.00';
	}
	if(!s){
		return '';
	}
	s = s + '';
	var l = s.split('.')[0].split('').reverse(),
		r = s.split('.')[1];
	var t = '';
	if(l){
		for(var i = 0; i < l.length; i++) {
			t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
		}
	}
	var re = '';
	if(r){
		if(r.length > 2){
			r = r.substr(0,2);
		} else {
			var l = r.length;
			while(l != 2){
				r = r + '0';
				l++;
			}
		}
		re = t.split('').reverse().join('') + '.' + r;
	}else{
		re = t.split('').reverse().join('') + '.00';
	}
	return re;
}
