until.thousandBitSeparator = function(s) {
	if(s == '0' || !s){
		return '0';
	}
	s = s + '';
	var l = s.split('.')[0].split('').reverse(),
		r = s.split('.')[1];
	var t = '';
	if(l){
		for(var i = 0; i < l.length; i++) {
			t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
		}
	}
	var re = '';
	if(r){
		if(r.length > 2){
			r = r.substr(0,2);
		} else {
			var l = r.length;
			while(l != 2){
				r = r + '0';
				l++;
			}
		}
		re = t.split('').reverse().join('');
	}else{
		re = t.split('').reverse().join('');
	}
	return re;
}

